@font-face {
  font-family: "Roboto-bold";
  src: url("./fonts/Roboto-Bold.ttf") format("truetype");
}
@font-face {
  font-family: "Roboto-medium";
  src: url("./fonts/Roboto-Medium.ttf") format("truetype");
}
@font-face {
  font-family: "Roboto-regular";
  src: url("./fonts/Roboto-Regular.ttf") format("truetype");
}

body {
  font-family: "Roboto-regular";
}

ul {
  padding: 0;
  margin: 0;
  li {
    list-style: none;
  }
}

a {
  display: inline-block;
  text-decoration: none;
}

.fixed-left-header {
  width: 65px;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  background: #0068ff;
  .logo {
    padding: 10px 14px;
    img {
      width: 100%;
    }
  }
  .nav-links ul {
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    height: 100vh;
    li {
      padding: 10px 15px;
      a {
        padding: 6px 6px 4px 6px;
      }
      img {
        width: 100%;
      }
    }
    li.active {
      a {
        border-radius: 5px;
        background: #165bbd;
      }
    }
  }
}
.container {
  width: calc(100% - 50px);
  margin: 0 auto;
  max-width: 1400px;
}
main,
header {
  padding-left: 65px;
}
header {
  padding: 16px 0 10px 65px;
  border-bottom: 1px solid #ddd;
  .container {
    display: flex;
    justify-content: space-between;
  }
  .all-projects {
    a {
      color: #9198a1;
      font-size: 14px;
      font-family: "Roboto-medium";
      &:hover {
        color: #666;
      }
      span {
        vertical-align: middle;
        margin-right: 15px;
      }
    }
  }
  .heading h1 {
    margin: 0;
    font-size: 20px;
    span {
      width: 6px;
      height: 6px;
      border: 2px solid #aaa;
      border-bottom: none;
      border-left: none;
      display: inline-block;
      transform: rotate(135deg);
      margin: 3px 3px 2px 7px;
    }
  }
  .header-links ul {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    .user-image {
      img {
        height: 30px;
        border-radius: 4px;
      }
    }
    .notification {
      padding: 0 20px;
      position: relative;
      img {
        height: 20px;
        box-shadow: 0 0 3px 0px #ddd;
      }
      .count {
        position: absolute;
        top: -8px;
        right: 10px;
        font-size: 10px;
        color: #fff;
        background: #ec300c;
        padding: 0 3px;
        border-radius: 9px;
      }
    }
    .search {
      img {
        height: 25px;
        border-radius: 4px;
        box-shadow: 0 0 4px 0px #f7f7f7;
      }
    }
  }
}

.dashboard {
  .dashboard-heading {
    padding: 25px 0;
    display: flex;
    .star-icon {
      padding-right: 5px;
    }
    .people-count {
      padding-right: 7px;
      .details {
        background: #f1f4f6;
        color: #9198a1;
        font-size: 12px;
        padding: 3px 5px;
        border-radius: 4px;
        font-family: "Roboto-medium";
        span {
          display: inline-block;
          vertical-align: middle;
          margin-right: 3px;
        }
      }
    }
    .days-left {
      .details {
        background: #ffe7d1;
        color: #ff8a26;
        font-size: 12px;
        padding: 3px 5px;
        border-radius: 4px;
        font-family: "Roboto-medium";
        span {
          display: inline-block;
          vertical-align: top;
          margin-right: 3px;
        }
      }
    }
    .show-my-tasks {
      margin-left: auto;
      font-size: 12px;
      color: #9198a1;
      font-family: "Roboto-medium";
    }
  }
}

.dashboard-data {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  .tasks-list-wrapper {
    width: 23.5%;
    background: #f3f5f7;
    border-radius: 7px;
    padding: 15px;
    box-sizing: border-box;
    .list-heading {
      margin-bottom: 15px;
      display: flex;
      justify-content: space-between;
      h2 {
        font-family: "Roboto-bold";
        font-size: 13px;
        margin: 5px 0 0;
        color: #333;
        font-weight: 400;
      }
      .more {
        span {
          display: inline-block;
          width: 4px;
          height: 4px;
          border-radius: 5px;
          margin-left: 3px;
          background: #777;
        }
      }
      .rec {
        margin-left: auto;
        margin-right: 10px;
        margin-top: 5px;
        font-size: 11px;
        color: #ec300c;
        font-family: "Roboto-medium";
        .circle {
          width: 8px;
          height: 8px;
          border-radius: 8px;
          background: #ec300c;
          display: inline-block;
          margin-right: 5px;
        }
      }
    }
  }
}
.tasks-list {
  ul li {
    margin-bottom: 15px;
  }
}
.task {
  padding: 15px 0;
  background: #fff;
  border-radius: 7px;
  position: relative;
  .delete-task {
    position: absolute;
    right: 5px;
    top: 5px;
    font-size: 8px;
    color: #888;
    cursor: pointer;
  }
  .task-title {
    padding-right: 15px;
    h3 {
      font-size: 13px;
      line-height: 21px;
      margin: 0;
      padding-left: 10px;
      color: #555;
      font-weight: 400;
      font-family: "Roboto-bold";
    }
  }
  .task-image {
    margin-top: 15px;
    img {
      width: 100%;
    }
  }
  .task-info {
    padding: 15px 15px 0 14px;
    display: flex;
    align-items: center;
    .info-with-icon {
      background: #f1f2f4;
      font-size: 12px;
      color: #888;
      padding: 3px 5px;
      border-radius: 4px;
      margin-right: 7px;
      span {
        display: inline-block;
        vertical-align: text-top;
        &:first-child {
          margin-right: 3px;
        }
      }
    }
    .users {
      margin-left: auto;
      display: flex;
      justify-content: flex-end;
      .users-icon {
        margin-left: 7px;
        img {
          height: 20px;
          border-radius: 4px;
        }
      }
    }
  }
}

.task.high {
  .task-title h3 {
    border-left: 4px solid #ff6554;
  }
}
.task.medium {
  .task-title h3 {
    border-left: 4px solid #ffd921;
  }
}
.task.low {
  .task-title h3 {
    border-left: 4px solid #aaa;
  }
}

/*------------ Checkbox Styling --------------*/
.checkbox {
  display: inline-block;
  vertical-align: middle;
  margin-left: 5px;
  input[type="checkbox"] {
    display: none;

    &:checked + label {
      background-color: #2f7df9;
    }

    &:checked + label:after {
      left: 18px;
    }
  }
  label {
    transition: all 200ms ease-in-out;
    display: inline-block;
    position: relative;
    height: 18px;
    width: 35px;
    border-radius: 20px;
    cursor: pointer;
    background-color: #aaa;
    color: transparent;
    &:after {
      transition: all 200ms ease-in-out;
      content: " ";
      position: absolute;
      height: 14px;
      width: 14px;
      border-radius: 50%;
      background-color: white;
      top: 2px;
      left: 2px;
      right: auto;
      box-shadow: 1px 1px 1px gray;
    }
  }
}

@media only screen and (min-width: 768px) and (max-width: 1023px) {
  .dashboard-data {
    flex-wrap: wrap;
    .tasks-list-wrapper {
      width: 48%;
      margin-bottom: 20px;
    }
  }
}
@media only screen and (max-width: 767px) {
  .dashboard {
    .dashboard-heading {
      flex-wrap: wrap;
      padding: 15px 0;
      .show-my-tasks {
        width: 100%;
        margin-top: 10px;
      }
    }
  }
  .dashboard-data {
    flex-wrap: wrap;
    .tasks-list-wrapper {
      width: 100%;
      margin-bottom: 20px;
    }
  }
}
.task .task-info .info-with-icon span.icon-check {
  display: none;
}

.completed-tasks .task .task-info .task-date {
  background: #1ec597;
  color: #fff;
  .icon-bell {
    display: none;
  }
  .icon-check {
    display: inline-block;
    margin-right: 5px;
  }
}

.add-new-task {
  position: fixed;
  bottom: 50px;
  right: 50px;
  padding: 9px 14px;
  background: #0068ff;
  color: #fff;
  border-radius: 100%;
  box-shadow: 0 0 7px #aaa;
  cursor: pointer;
}
.popup-content {
  border: none !important;
  background: transparent !important;
}
.new-task-wrapper {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 999;
  overflow-y: scroll;
  .add-new-task-form {
    width: 45%;
    max-width: 500px;
    box-sizing: border-box;
    margin: 100px auto 50px;
    padding: 10px 50px 30px;
    background: #fff;
    border-radius: 10px;
    border: 1px solid #eee;
    position: relative;
    form {
      padding-top: 30px;
    }
    .heading {
      padding: 10px 0 20px;
      border-bottom: 1px solid #ddd;
      text-align: center;
      span {
        font-weight: 600;
        color: #aaa;
        position: absolute;
        right: 30px;
        top: 20px;
        font-size: 12px;
      }
      .close-pop-up {
        cursor: pointer;
      }
      h2 {
        margin: 0;
        font-size: 20px;
        font-weight: 400;
        font-family: "Roboto-medium";
        color: #444;
      }
    }
    .input-field {
      display: flex;
      margin-bottom: 15px;
      label {
        font-size: 12px;
        font-family: "Roboto-medium";
        color: #666;
        min-width: 100px;
      }
      input,
      textarea,
      select {
        flex-grow: 1;
        width: 50%;
        border: 1px solid #ccc;
        padding: 7px 10px;
        box-sizing: border-box;
        font-family: "Roboto-regular";
        &:focus {
          border: 1px solid #0068ff;
          outline: none;
        }
      }
      textarea {
        height: 100px;
      }
    }
    .submit-btn {
      text-align: center;
      margin-top: 30px;
      button {
        background: #0068ff;
        outline: none;
        border: 1px solid #0068ff;
        padding: 7px 15px;
        border-radius: 4px;
        color: #fff;
        font-family: "Roboto-medium";
        text-transform: uppercase;
        cursor: pointer;
      }
    }
  }
}
@media only screen and (max-width: 767px) {
  .container {
    width: calc(100% - 30px);
  }
  header .header-links ul .user-image img {
    height: 25px;
  }
  header .header-links ul .notification {
    padding: 0 13px;
  }
  header .container {
    flex-wrap: wrap;
    .all-projects {
      order: 1;
    }
    .heading {
      order: 3;
      width: 100%;
      margin-top: 7px;
      text-align: center;
      h1 {
        font-size: 16px;
      }
    }
    .header-links {
      order: 2;
    }
  }
  .new-task-wrapper .add-new-task-form {
    width: 80%;
  }
  .new-task-wrapper .add-new-task-form .input-field {
    flex-wrap: wrap;
    label {
      margin-bottom: 5px;
    }
    label,
    input,
    textarea,
    select {
      width: 100%;
    }
  }
}
